.btn {
  width: 100%;
  height: 70px;

  cursor: pointer;

  text-align: left;
  padding-left: 5%;
  background: transparent;
  border: none;
  border-radius: 5px;

  display: flex;
  align-items: center;
  gap: 25%;
}

.btn:hover {
  background-color: #098fa6;
  color: white;
  font-weight: bold;
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.1);
}
