@import url('https://fonts.googleapis.com/css2?family=Fondamento&display=swap');
.header {
  width: 100%;
  height: 10vh;
  /* background-color: red; */
  display: flex;
  gap: 0%;
  /* background-color: #f0f0f0; */
}

.titleDiv {
  width: 20%;

  display: flex;
  /* background: red; */
}

.logo {
  width: 25%;
}

.logoText {
  line-height: 13px;
  /* font-family: 'Fondamento', cursive; */
  font-weight: bold;
}

.searchBar {
  /* background-color: #098ea621; */
  display: flex;
  width: 100%;

  position: relative;
  justify-content: center;
  align-items: center;
}

.main_searchBox {
  width: 30%;
  height: 100%;
  /* background: orange; */
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.searchBarThings {
  /* background-color: blue; */
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50%;
  position: relative;
}

.search_query {
  width: 100%;
  /* border-style: none; */
  border-bottom-style: solid;
  border-radius: 10px;
  border-color: rgba(128, 128, 128, 0.457);
  text-align: center;
}

.search_submit {
  /* display: none; */
  position: absolute;
  height: 100%;
  right: 0%;
  width: 50px;
  background-image: url('../../public/images/search.svg');
  background-repeat: no-repeat;

  border-style: none;
  background-color: transparent;
  background-position: 10%;
  cursor: pointer;
}

.selectButtons {
  display: none;
  position: absolute;
  height: 20%;
  left: 0;
}
